//store   
//vuex 

//vuex
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

// Vuex

/*  1.state vuex */
var state={
	num:1,
	info:[],
	username:'',
	order:[]
}

// 2.mutationstate
var mutations={
	Count(){
		++state.num;
	},
	//state
	storeItemInfo(state,data){
		state.info=data;
//		state.list.push(data);
	},
	saveuser(state,data){
		state.username = data;
	},
	saveOrder(state,data){
		state.order.push(data);
	}
}
 
 // 3.statecountgetters
 var getters={
  computedCount: (state) => {
    return state.num*2;
  }
}
 
// 3.vuex Vuex.Store
const store = new Vuex.Store({
  state,  /*  */
  mutations,
  getters
})


//4.store
export default store;


